button {
  height: 70%;
  margin-left: 2%;
  margin-right: 2%;
  padding-left: 2%;
  padding-right: 2%;
  border-radius: 50vw;
  font-size: medium;
  color: white;
  background-color: black;
  transition: 0.3s;
}

@media only screen and (max-width: 541px) {
  button {
    height: 2.6rem;
    font-size: small;
  }
}

@media only screen and (max-width: 410px) {
  button,
  .input-field {
    height: 2rem;
    font-size: x-small;
  }
}

@media only screen and (max-width: 320px) {
  button,
  .input-field {
    font-size: xx-small;
  }
