button:hover {
  font-style: bold;
  border-width: 0.3vh;
  cursor: pointer;
  transition: 0.3s;
}

.buttonStyle1:hover {
  border-color: green;
  color: green;
}

.buttonStyle2:hover {
  border-color: peru;
  color: peru;
}

#reset {
  opacity: 0.6;
  background-color: orangered;
}
#reset:hover {
  opacity: 0.9;
}

button:disabled,
.buttonStyle1:disabled,
.buttonStyle2:disabled,
#reset:disabled {
  background-color: grey;
  color: white;
  border-width: 0vw;
  opacity: 1;
  cursor: not-allowed;
}

.bar-container {
  width: 100%;
  height: 74%;
  margin-left: -1%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bar {
  width: 4vw;
  height: 70vh;
  margin: 0 1.5%;
  transform-style: preserve-3d;
  transform: rotateX(-20deg) rotateY(-30deg);
  background-color: rgba(254, 254, 254, 0.25);
}

.side {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(254, 254, 254, 0.25);
}

.back {
  transform: translateZ(-4vw);
}

.left {
  transform-origin: left;
  transform: rotateY(90deg);
}

.right {
  transform-origin: right;
  transform: rotateY(-90deg);
}

.top {
  height: 4vw;
  transform-origin: top;
  transform: rotateX(-90deg);
  background-color: rgba(254, 254, 254, 0.15);
}

.bottom {
  height: 4vw;
  transform-origin: top;
  transform: translateY(70vh) rotateX(-90deg);
  background-color:rgba(46, 211, 39, 1);
  box-shadow: 5px 5px 50px 5px rgba(46, 211, 39, 0.2);
  transition: 0.3s;
}

.color-bar {
  background-color: rgba(46, 211, 39, 1);;
  box-shadow: 5px 5px 50px 5px rgba(46, 211, 39, 0.2);;
  transition: 0.3s;
}

